import React, { useEffect, useState } from 'react'
import Breadcumb from '../../../Component/Breadcumb'
import Sidebar from '../Sidebar'
import { Link, useNavigate } from 'react-router-dom'
import FormValidator from '../../../Validator/FormValidator'
import ImgValidator from '../../../Validator/ImgValidator'
import { useDispatch, useSelector } from 'react-redux'
import { getTestimonial, createTestimonial } from "../../../Redux/ActionCreators/TestimonialActionCreator"
export default function AdminCreateTestimonial() {
    //let[TestimonialStateData,setTestimonialStateData]=useState([])
    let [data, setData] = useState({
        name: "",
      
        pic: "",
        message:"",
        active: true
    })
    let [errorMessage, seterrorMessage] = useState({
        name: "Name Field Is Mendatory",
        message: "Message Field Is Mendatory",
        pic: "Pic Field Is Mendatory",
    })
    let [show, setShow] = useState(false)
    let navigate=useNavigate()
    let TestimonialStateData = useSelector(state => state.TestimonialStateData)
    let dispatch = useDispatch()

    function getInputData(e) {
        var name = e.target.name
        var value = e.target.files && e.target.files.length ? "testimonial/" + e.target.files[0].name : e.target.value
        //var value = e.target.files && e.target.files.length ?e.target.files[0] : e.target.value
        seterrorMessage((old) => {
            return {
                ...old,
                [name]: e.target.files ? ImgValidator(e) : FormValidator(e)
            }
        })
        setData((old) => {
            return {
                ...old,
                [name]: name === "active" ? (value === "1" ? true : false) : value
            }
        })
    }
    function PostInputData(e) {
        e.preventDefault()
        let error = Object.values(errorMessage).find(x => x !== "")
        if (error)
            setShow(true)
        else {
            let item=TestimonialStateData.find(x=>x.name.toLowerCase()===data.name.toLowerCase())
            if(item){
                setShow(true)
                seterrorMessage((old)=>{
                    return{
                        ...old,
                        'name':'Testimonial With Same Name Already Exist'
                    }
                })
                return
            }
            dispatch(createTestimonial({ ...data }))
            // let formData = new FormData() 01/04/2025
            // formData.append("name", data.name)
            // formData.append("pic", data.pic)
            // formData.append("message", data.message)
            // formData.append("active", data.active)
            // dispatch(createTestimonial(formData))
            navigate("/admin/testimonial")

        }
    }
        useEffect(() => {
            dispatch(getTestimonial())
        }, [TestimonialStateData.length])
    return (
        <>
            <Breadcumb title="Admin" />
            <div className='container-fluid my-3'>
                <div className="row">
                    <div className="col-md-3">
                        <Sidebar />
                    </div>
                    <div className="col-md-9">
                        <h5 className='bg-primary w-100 p-2 text-center text-light'>Testimonial<Link to="/admin/testimonial"><i className='fa fa-long-arrow-left text-light float-end'></i></Link></h5>
                        <form onSubmit={PostInputData}>
                            <div className='mb-3'>
                                <label>Name*</label>
                                <input type='text' name='name' onChange={getInputData} placeholder='Testimonial Name' className={`form-control border-3 ${show && errorMessage.name ? 'border-danger' : 'border-primary'}`} />
                                {show && errorMessage.name ? <p>{errorMessage.name}</p> : null}
                            </div>
                            <div className="mb-3">
                                <label>Message*</label>
                                <textarea name="message"spellCheck='false'  onChange={getInputData} className={`form-control border-3 ${show && errorMessage.message?'border-danger':'border-primary'}`} placeholder='Message....'rows={5}></textarea>
                                {show && errorMessage.message ? <p>{errorMessage.name}</p> : null}
                            </div>
                            <div className="row">
                                <div className="col-md-6 mb-3">
                                    <label>Pic*</label>
                                    <input type='file' name='pic' onChange={getInputData} className={`form-control border-3 ${show && errorMessage.pic ? 'border-danger' : 'border-primary'}`} />
                                    {show && errorMessage.pic ? <p>{errorMessage.pic}</p> : null}
                                </div>
                                <div className="col-md-6 mb-3">
                                    <label>Active*</label>
                                    <select name="active" onChange={getInputData} className='form-select border-3 border-primary'>
                                        <option value="1">Yes</option>
                                        <option value="0">No</option>
                                    </select>
                                </div>
                            </div>
                            <div className='mb-3'>
                                <button type='submit' className='btn btn-primary text-center w-100 p-2 text-light'>Create</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
       </>
    )
}
